
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
 
  }
  @font-face {
    font-family: NoeDisplay-Bold;
    src: url(/src/fonts/FontsFree-Net-NoeDisplay-Bold.ttf);
  }
  :root {
    --light-black: #383A3D;
    --light-red: #EA4862;
    --dark-red:#EB394E;
    --dark-grey:#838A91;
    --light-grey:#ECEEEF;
  }

  .footer{
   display: grid;
grid-template-columns: 1fr 2fr;

align-items: center;
text-align: center;

  }
.smicons{
    margin-right: -50%;
    margin-left: 30%;
    font-size: 30px;
    
}
.socialicon:hover{
  color: var(--dark-red);
}
 .footer a{
margin-right: 5%;
 }

 @media screen and (max-width: 990px) {

  .smicons{
    font-size: 20px;

  }
  .footer{

    margin-bottom: 1%;

  }
  .footer p{
    font-size: 15px;
    margin-left: 10%;
  }

 }
