* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  /* ========== HEADER STYLING ========== */


  @font-face {
    font-family:NunitoSans-Regular ;
    src: url(/src/fonts/Nunito_Sans/NunitoSans-Regular.ttf);
  }
:root {
  --light-black: #383A3D;
  --light-red: #EA4862;
  --dark-red:#EB394E;
  --dark-grey:#838A91;
  --light-grey:#ECEEEF;
}

.tools-section{
  margin-top: -5%;
height: 100vh;

}
.Toolbox-grid{
  display: grid;
  grid-template-columns: 1fr 1fr;
 margin-top: 5%;
 margin-bottom: 5%;
}
.skills-title{
  margin-left: 5%;
  margin-top: 5%;
  margin-bottom: 5%;
}

.Toolbox-card > img{
  width: 70px;
  height: 70px;
}
.Toolbox-card {
text-align: center;
/* animation-duration: 15s;
animation-iteration-count: infinite;
animation-timing-function: ease-in-out;
