.skills-title h1{
  color: var(--dark-red);
}
.skills-title h3{
  margin-right: 1%;
}






@media (min-width: 1200px){
  .Toolbox-grid{
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
  .skills-title h1 {
font-size: 80px;
  }
  .section-title-h2 {
font-size: 25px;
  }
  .skills-title h3 {
font-size: 35px;
  }
}

@media (min-width: 992px){
  .Toolbox-grid{
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
  .skills-title h1 {
    font-size: 50px;
      }
      .section-title-h2 {
    font-size: 15px;
      }
      .skills-title h3 {
    font-size: 25px;
      }
      .tools-section{
        margin-top: 10%;
     
      }
}

@media (max-width: 992px){
  .Toolbox-grid{
    grid-template-columns:  1fr 1fr 1fr ;
    margin-top: 5%;
  }

    .skills-title h1 {
      font-size: 35px;
    }
    .section-title-h2 {
      font-size: 10px;
    }
    .skills-title h3 {
   
      font-size: 20px;
      margin-right: 2%;
    }
    .tools-section{

      height: 100vh;
      padding-top: 1rem;

    }
}

