/* ======= CONTACT ======= */
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  @font-face {
    font-family:NunitoSans-Regular ;
    src: url(/src/fonts/Nunito_Sans/NunitoSans-Regular.ttf);
  }
  :root {
    --light-black: #383A3D;
    --light-red: #EA4862;
    --dark-red:#EB394E;
    --dark-grey:#838A91;
    --light-grey:#ECEEEF;
  }
  

#contact {
    padding-top: 50px;

height: 100vh;
}

.contact-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: center;
    margin: 7%;
}


.email-icon {
    width: 100px;
    height: 100px;
    line-height: 100px;
    max-width: 100%;
    font-size: 55px;
    text-align: center;
    vertical-align: middle;
    position: relative;
    margin: 0 auto 10px;
}

#contact p {
    font-size: 16px;
    font-weight: 300;
    max-width: 450px;
    margin: 0 auto;
   
}

#contact #email-button {
    
    padding: 10px 20px;
    text-decoration: none;
    font-family:NunitoSans-Regular ;
    font-size: 20px;
    /*border-radius: 30px;*/
    border: 4px solid white;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
  border-radius: 9px;
  box-shadow: 2px 2px 2px 2px var(--dark-grey);
}

#contact #email-button:hover {
    color: #fff;
    background: var(--light-red);
    border: 2px solid var(--dark-red);
}

.sayhello{
    margin-bottom: 40px;
 
}

@media screen and (max-width: 480px) {
    #contact {
        padding: 15px 10px 50px;
    }
    .contact-container{
      grid-template-columns: 1fr;
      
    }
}

@media screen and (max-width: 990px) {
    #contact {
  height: 100vh;
    }
  
    .row{
      margin-bottom: 10%;
      margin-top: 5%;
    }
}
