/* ---------- form styling ---------- */

form {
    margin: auto;
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.25);
    text-align: center;
    padding: 1em 1em 2em;
    background-color: var(--white);
    max-width: 380px;
    animation: fadeIn 0.5s;
}

form button {
    padding: 12px 15px;
    font-size: 1em;
    width: 100%;
    max-width: 350px;
}

button {
    font-weight: 300;
    text-align: center;
    cursor: pointer;
    border: none;
    color: var(--text-color-light);
    background-color: var(--green);
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 0.5em 1em;
    font-size: 0.8em;
    display: block;
    margin: 1em auto 0;
}

button.btn-outline {
    background-color: var(--light-green);
    color: var(--green);
    border: solid 2px var(--green);
}

.btn-sign-out {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    font-size: 0.7em;
    padding: 2.032em 1em;
}

input {
    margin: 1em auto;
    width: 100%;
    max-width: 350px;
    padding: 1em;
    box-sizing: border-box;
    display: block;
    background: var(--white);
    border: solid 2px var(--green);
}

.image-preview {
    width: 100%;
    max-width: 350px;
    height: 200px;
    object-fit: cover;
    margin: auto;
    display: block;
    cursor: pointer;
}

form .file-input {
    display: none;
}

/* ---------- loader styling ---------- */
.loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 99999999;
    overflow: hidden;
    background: var(--loader-background-color);
    text-align: center;
}

.spinner {
    border: 5px solid var(--light-green);
    border-radius: 50%;
    border-top: 5px solid var(--green);
    width: 50px;
    height: 50px;
    -webkit-animation: spin 1s linear infinite;
    /* Safari */
    animation: spin 1s linear infinite;
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.hide {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 0.4s, opacity 0.4s linear;
}

.avatar {
    display: flex;
    align-items: center;
    margin: 0 0 1.2em;
    text-align: left;
}

.avatar span {
    margin: 0 0 0 1em;
}

.avatar h3,
.avatar p {
    margin: 0;
}

article .avatar img {
    max-width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
}



.post-row {
    /* background: rgb(241, 241, 244); */
    margin-bottom: 40px;
    overflow: hidden;
    
}

.post-row > article {
    padding: 40px;
    /* background: rgb(241, 241, 244); */
    max-width: 400px;
    margin: auto 0 auto auto;
    text-align: left;
    font-size: 20px;
    
}
.post-row > article > h3{
    font-size: 35px;
    margin-bottom: 10%;
}



@media (min-width: 768px) {
    .post-row {
        display: grid;
        grid-template-columns: 3fr 4fr;
    }
  
}

.post-row a {
    color: var(--light-red);
    font-size: 0.85em;
    font-weight: 700;
    letter-spacing: 0.2em;
    line-height: 1.8;
    margin-top: 1em;
    text-transform: uppercase;
}

@media (min-width: 768px) {
    .post-row a {
        font-size: 0.9em;
    }
}

.post-row img {
    width: 100%;
    height: 100%;

    object-position: center;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    overflow: hidden;
}

.post-row:hover img {
    -moz-transform: scale(1.1);
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

@media (min-width: 990px) {
    .post-row figure {
        height: 450px;
    }
}

.post-row figure {
    padding: 0;
    margin: 0;
    overflow: hidden;
}


/* 3d and rgb effect */

  .card {
    transform-style: preserve-3d;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
  }
