@font-face {
    font-family:NunitoSans-Regular ;
    src: url(/src/fonts/Nunito_Sans/NunitoSans-Regular.ttf);
  }
:root {
  --light-black: #383A3D;
  --light-red: #EA4862;
  --dark-red:#EB394E;
  --dark-grey:#838A91;
  --light-grey:#ECEEEF;
}






.toggle-button{
    font-size: 25px;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    height: 45px;
    z-index: 99;
    color: white;
    color: var(--light-black);
  
}

.toggle-button-conatiner{
    background-color: white;
    border-radius:50%;
 padding-left: 13px;
 padding-right: 13px;
 padding-top: 2px;

}


@media (max-width: 800px){
    .toggle-button{
       z-index: 99;
   
    }
.toggle-button-conatiner{
    margin-left: auto; 
    margin-right: 10px;
}



}
   
    
