
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
@font-face {
  font-family: NoeDisplay-Bold;
  src: url(/src/fonts/FontsFree-Net-NoeDisplay-Bold.ttf);
}
:root {
  --light-black: #383A3D;
  --light-red: #EA4862;
  --dark-red:#EB394E;
  --dark-grey:#838A91;
  --light-grey:#ECEEEF;
}


.Navbar {
  height: 60px;
 display: flex;
  justify-content: space-between;
  align-items: center;
margin: 0 5% 2% 5%;
 z-index: 99;
 padding-top: 5%;



}


.nav-logo-span{
  display: flex;
  justify-content: center;

}
.nav-logo-span a { 
line-height:18px;
}
.nav-logo-span img {
 width: 50px;
margin-top: -10%;
}

.Navbar > .nav-logo {
  font-weight: 700;
  font-size: 17px;
  margin: 15px 0px 15px 15px;
  color: var(--light-black);
  text-decoration: none;
 
}

.nav-items{
display: flex;
margin-right: 11%;
}
.Navbar img {
border-radius: 50%;
  height: 40px;


}
.Nav-span{
  margin-right: 50%;
  margin-left: 5%;
  text-decoration: none;
  font-style: bold;
  font-family: Malayalam MN;
 
}
.Nav-span:hover{
  color: var(--dark-red);
}

.Navbar > .nav-items > .navlink {
 
  font-size: 20px;
font-weight: bold;
  text-decoration: none;
  margin: 10px;
  position: relative;
  opacity: 0.9;

}



.Navbar > .nav-items > .navlink:hover {
  opacity: 1;
}

.Navbar > .nav-items > .navlink::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 3px;
  background: var(--dark-red);
  transition: all 0.45s;
}
.active{
  background: var(--dark-red);
  color: #ffffff;
  border-radius: 2px;
 padding-left: 5px;
 padding-right: 5px;
}

.Navbar > .nav-items > .navlink:hover::before {
  width: 100%;
  
}

.Navbar > .nav-toggle {
  display: none;
 
}





@media (max-width: 800px) {
  .Navbar > .nav-items {
    position: absolute;
    top: 50px;
    display: flex;
    flex-direction: column;
    background:white;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: all .45s;
    padding-top: 20%;
    
  }

  
  .Navbar > .nav-items > .navlink{
    color: var(--light-black);
    text-align: center;
    padding-bottom: 5%;
    font-family: NoeDisplay-Bold;
    font-size: 2em;
   
  }


  .Navbar > .nav-items > .navlink::before {
      background:var(--dark-red);
      
  }

  .Navbar > .nav-items.open {
      transform: translateX(0);
     z-index: 2;
  }

  .nav-items.open{
 margin-top: -10%;
 
  }
.Nav-span{
  margin-left: 1%;
  
}
  .Navbar > .nav-toggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
   
    background-color: white;
    border-radius: 50%;
  }

  

  .nav-toggle > .bar {
    position: relative;
    width: 32px;
    height: 2px;
  
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle > .bar::before,
  .nav-toggle > .bar::after {
    content: "";
    position: absolute;
    height: 5px;
    background: var(--light-black);
    border-radius: 2px;
    transition: all 0.45s ease-in-out;
    
  }

  .nav-toggle > .bar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
    
  }

  .nav-toggle > .bar::after {
    width: 32px;
    transform: translateY(8px);
   
  }

  .nav-toggle.open > .bar {
      transform: translateX(-40px);
      background: transparent;
      z-index: 3;
  }

  .nav-toggle.open > .bar::before {
      width: 32px;
      transform: rotate(45deg) translate(26px, -26px);
     
  }

  .nav-toggle.open > .bar::after {
    transform: rotate(-45deg) translate(26px, 26px);
   
  }


}

