/* ========== HEADER STYLING ========== */
body{
 font-family:NunitoSans-Regular ;
  
}


@font-face {
  font-family:NunitoSans-Regular ;
  src: url(/src/fonts/Nunito_Sans/NunitoSans-Regular.ttf);
}
:root {
  --light-black: #383A3D;
  --light-red: #EA4862;
  --dark-red:#EB394E;
  --dark-grey:#838A91;
  --light-grey:#ECEEEF;
}
 
.headerandarrow{
  align-items: center;
  text-align: center;
  height: 40em;
display: flex;
flex-direction: column;
}
.header {

  
    align-items: center;
    text-align: center;
    width: 90%;
margin-bottom: -4%;


  }
  .vertical-center{
    height: 15em;
    padding-top: 1em;
  }
  .hi-sec{
    display: flex ;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 10%;

    
  }
  .ahmed-emoji{
    height: 15em;
   
  }
  .header h3{
   margin-bottom: -2%;
   font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   
  }
 
.header a{

font-weight: 100;

    padding-top: 8%;
   text-decoration: none;
   align-items: center;
  
}
.header a:hover{
color: var(--dark-red);

  }
  .header h2, h3{

  margin-top: 3%;
 
  }
  .header h1 h2 {
    font-weight: 700;
    font-family: NunitoSans-Regular ;
  }
  
  .page-scroll{
    letter-spacing: 1px;
    font-size: 46px;
  margin-top: 10%;

  }
  .slide-in{
    text-align: left;
  
  }

  @media (max-width: 768px){
   .header a, h1, h2,  h3 {
margin-bottom: 5%;
margin-right: -2%;
margin-left: 1%;
   } 
   .slide-in{
    padding-left: 6%;
   }
   .header h1 {
    font-size: 50px;
    letter-spacing: 3px;
    
   }
   .header h2 {
    font-size: 45px;
  margin-top: -25px;
  
  }
  .header h3{
    font-size: 25px;
   
   
  }
  .vertical-center{
    margin-top: 20%;
  }
  .ahmed-emoji{
height: 110px;

   
  }

  }
  @media (min-width: 768px) {
    .header h1 {
      font-size: 80px;
    
        
    }
    .header h2 {
      font-size: 75px;
      margin-top: -10%;
    }
  .header h3 {
    font-size: 35px;
  }
  .ahmed-emoji{
    height: 150px;
    margin-top: 5%;
       
      }
      .slide-in{
        padding-left: 20%;
       }
 
  }
  
  @media (min-width: 992px) {

    .header h1 {
      font-size: 120px;
    
        
    }
    .header h2 {
      font-size: 105px;
      margin-top: -10%;
    }
  .header h3 {
    font-size: 45px;
    margin-top: 1%;
  }
  .ahmed-emoji{
    height: 220px;
    margin-top: 8%;
       
      }
    
        .slide-in{
          padding-left: 1%;
         }
      
    }
  
  
.vertical-center {
  align-items: center;
 text-align: center;

  }
  
  
  @media (min-width: 992px) {
    .slide-in{
      padding-left: 22%;
    }
    
  }
  
  @media (min-width: 1200px) {
    
    .slide-in{
      padding-left: 29%;
     }

  }
  
  @media (min-width: 1400px) {
  
   
  }


  /* Header animation */

  .slide-in-bottom1 {
    -webkit-animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }

  .slide-in-bottom2 {
    -webkit-animation: slide-in-bottom 2.0s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: slide-in-bottom 2.0s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }

  .slide-in-bottom3 {


    -webkit-animation: slide-in-bottom 2.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: slide-in-bottom 2.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }
  
 

  



