html, body{
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}



:root {


  --text-primary-light: #383A3D;
  --text-primary-dark: white;

  --text-secondary-light: #383A3D;
  --text-secondary-dark: white;

  --text-navbar-light: #383A3D;
  --text-secondary-dark: white;

  --bg-primary-light:white;
  --bg-primary-dark: #09090a;

  --bg-secondary-light: rgb(212, 212, 212);
  --bg-secondary-dark:#151B1F;

  --bg-navbar-light: white;
  --bg-navbar-dark: #383A3D;

  --link-primary-light: #303132;
  --link-primary-dark: #838A91;
}

.light-theme {
  --text-primary: var(--text-primary-light);
  --text-secondary: var(--text-secondary-light);
  --bg-primary: var(--bg-primary-light);
  --bg-secondary: var(--bg-secondary-light);
  --bg-navbar: var(--bg-navbar-light);
  --link-primary:var(  --link-primary-light)
}

.dark-theme {
  --text-primary: var(--text-primary-dark);
  --text-secondary: var(--text-secondary-dark);
  --bg-primary: var(--bg-primary-dark);
  --bg-secondary: var(--bg-secondary-dark);
  --bg-navbar: var(--bg-navbar-dark);
  --link-primary:var(  --link-primary-dark)
}
.App {

  background-color: var(--bg-primary);

  color: var(--text-primary);
}

