* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  /* ========== HEADER STYLING ========== */

  @font-face {
    font-family:NunitoSans-Regular ;
    src: url(/src/fonts/Nunito_Sans/NunitoSans-Regular.ttf);
  }
:root {
  --light-black: #383A3D;
  --light-red: #EA4862;
  --dark-red:#EB394E;
  --dark-grey:#838A91;
  --light-grey:#ECEEEF;
}
/* ======= ABOUT ======= */

#about {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding-top: 90px;
  padding-bottom: 100px;
  width: 90%;
 height: 100%;
 margin-left: 3%;
 margin-right: 2%;
}


#about h2 {
  font-family: NunitoSans-Regular;
  text-align: center;
  margin-bottom: 3%;
  font-size: 2.5em;
  color: var(--dark-red);
  
}

.about-h2-emoji h2{
  font-weight: 900;
}



#about p {
  margin: 30px auto;
  color: var(--light-black);
  text-align: center;
}



.article-about{
  font-size:1.5em ;
  width: 100%;
  text-align: center;
}
#about a {
  font-weight: 500;
  text-decoration: none;
  
}

#about img {
 
  -webkit-filter: grayscale(80%);
  filter: grayscale(80%);
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
width: 100%;

}

.container{
  display: grid;
  grid-template-columns: 1fr 1fr; 
  justify-content: center;
}
.title-img{
  width: 100%;
  align-items: center;
  text-align: center;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;

}

#about img#prof-pic {
  width:60%;
    height: auto;

   
  }
.about-img{
    z-index: 9999;
   
  }

@media screen and (max-width: 1100px) {
  
  
  .container{
    grid-template-columns: 1fr 1fr;
    
  }
  
}

@media screen and (max-width: 890px) {

  
  .container{
    grid-template-columns: 1fr;
  }

  #about img#prof-pic {
    width:50%;
      height: auto;

     
    }
    .title-img{

    }
    
}

@media screen and (min-width: 1200px){

  #about h2{
    font-size: 50px;
  }

}
 


@media screen and (min-width: 890px) {
  #about img#prof-pic {

      height:100%; 
    }

.title-img{
height: 70%;
align-items: center;
justify-content: center;
margin-top: 10%;

}


}

@media screen and (max-width: 500px) {
  #about {
      padding: 60px 10px;
  }
 
}

@media screen and (max-width: 480px) {
  #about img#prof-pic {
      width: 150px;
  }
 

 
}

@media screen and (max-width: 360px) {

}

.section-title-h2{
  text-align: left;
  margin-left: 10%;
}

@media (min-width: 1200px){
 
  .section-title-h2 {
font-size: 25px;
  }
}

@media (min-width: 992px){
 
      .section-title-h2 {
    font-size: 15px;
      }
}

@media (max-width: 992px){

    .section-title-h2 {
      font-size: 10px;
    }
   
}


